*{
  font-family: 'Press Start 2P', cursive;
  color: #fff;
  
}

body{
  padding: 60px;
  background-color: #121212;
}
.tittle{
  text-shadow: 1px 1px 20px dodgerblue;
}

input{
  color: #282c34;
  width: 99%;
  font-size: 1.1rem;
  padding: 8px;
  font-size: smaller;
  outline: none;
  border-radius: 4px;
  box-sizing: border-box;
  transition: 2s;
  border: 1px solid #fff;
}
input:focus{
  border-color: dodgerblue;
  box-shadow: 0 0 10px 0 dodgerblue;
}
.categories{
  padding-top: 20px;
  text-shadow: 0 0 20px #00FF00;
}

.Card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Card{
  align-content: center;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  }
  .Card p{
    font-size: 10px;
    color: aliceblue;
    font-family: 'Press Start 2P', cursive;
    padding: 5px;
    text-align: center;
  }
  .Card img{
    max-height: 300px;
   border-radius: 10px;

  }


